.main-content{padding:50px 0px}.intro_section_container{display:flex;align-items:center}.intro_section_container .main-image{filter:drop-shadow(2px 4px 36px rgba(255, 255, 255, 0.168627451))}.intro-section{display:flex;gap:50px;justify-content:space-between}.intro-section h1{font-size:32px;color:#cfcfcf;margin-bottom:25px}.intro-section span{color:#575757}.intro-section .main-image{width:231px;height:254px}@keyframes anime{0%{transform:translateY(10px)}50%{transform:translateY(30px)}100%{transform:translateY(10px)}}.intro-section img{animation:anime 4s ease infinite;width:200px;height:200px;filter:drop-shadow(1px 1px 56px rgba(255, 255, 255, 0.3882352941));image-rendering:pixelated;transform:scale(0.999)}@media(max-width: 990px){.intro-section{margin-bottom:30px}.intro-section img{display:none}}.form-section{display:flex;color:#cfcfcf;margin:30px 0}.form-section .gender{margin-right:44px}.form-section .gender-label{margin-bottom:22px;display:block;font-weight:700}.form-section :where(.gender-options,.search-gender-options) input{color:#cfcfcf;width:47px;height:47px;border:1px solid #242424;border-radius:11px;margin-right:10px;background-color:#0d0d0d}@media(max-width: 500px){.form-section .gender{margin-right:20px}}.search-button{padding:12px 33px;border-radius:11px;border:none;background-color:#1e1e1e;color:#cfcfcf;font-weight:700;transition:.3s ease;cursor:pointer}.search-button:hover{background-color:#313131}.border.selected{border:1px solid #276aeb !important;transition:.3s ease;background:linear-gradient(46deg, rgba(34, 92, 255, 0.2), rgba(255, 255, 255, 0))}.text-input{border-radius:11px;border:1px solid #2d2d2d;background-color:#111;padding:12px 33px;color:#fff;outline:none;max-width:230px}.search-name{display:flex;gap:22px}.search-name span{color:#cfcfcf}.roulette{color:#fff;display:flex;gap:20px;max-width:200px;background:#1e1e1e;justify-content:center;padding:12px 33px;border-radius:10px}.roulette span{color:#737373;font-size:14px;cursor:pointer}.roulette .roulette-chat{color:#cfcfcf}.roulette .roulette-call{position:relative}.roulette .roulette-call p{position:absolute;bottom:-55px;background:#b3b3b3;width:130px;padding:10px;left:50%;right:50%;transform:translate(-50%);text-align:center;border-radius:10px;z-index:-1;opacity:0;transition:all .7s;color:#363636}.roulette .roulette-call:hover p{opacity:1;z-index:0}